import { url } from "inspector";
import { Clue } from "./clue";
import { Row, RowState } from "./Row";
import { gameName, maxGuesses } from "./util";
import { WordOfTheDay, WotdInfo } from "./wordOfTheDay";

var he = require('he');
const wod_url = `https://www.google.com/search?q=${he.encode(WordOfTheDay)}`

interface PrizeProps {
  won: boolean
}

export function Prize(props: PrizeProps) {
  return (
    <div className="App-about">

      <div
        style={{
          visibility: props.won === true ? "visible" : "hidden",
        }}>well done you guessed '{WordOfTheDay}' </div>

      <div
        style={{
          visibility: props.won === false ? "visible" : "hidden",
        }}>todays name was '{WordOfTheDay}'</div>

      <p>
        {WotdInfo}
      </p>

      Click here to read more about <a href={wod_url} target='_blank'>{WordOfTheDay}</a>

    </div>
  );
}
